<template>
	<div id='editor' class="warp">
		<nav class="nvs">
			<el-breadcrumb separator=">">
				<el-breadcrumb-item :to="{ path: '/shop' }">店铺管理</el-breadcrumb-item>
				<el-breadcrumb-item :to="{path:'/shop/shopinfo'}">店铺信息</el-breadcrumb-item>
				<el-breadcrumb-item>编辑基础信息</el-breadcrumb-item>
			</el-breadcrumb>
		</nav>
		<section class="basicinformation">
			<div class="title">
				编辑基础信息
			</div>
			<div class="info-warp">
				<div class="shopTitle">{{shopinfo.shop_name}}</div>
				<div class="shopInfo">
					<p class="shops">
						<span class="shopId">店铺ID</span>
						<span class="shopNumber">{{shopinfo.shop_id}}</span>
					</p>
				</div>
				<el-form ref="form" label-width="90px" :model='form'>
					<el-form-item label="店铺名称">
						<el-input v-model='form.shop_name'></el-input>
					</el-form-item>
					<el-form-item label="商户标志">
							<div class="shoplogo left" v-if="shoplogo.length==0">
								<img src="../../img/shop_icon.png" />
							</div>
							<div class="shoplogo left" v-else>
								<div class="bg"></div>
								<img :src="imgbase_url+'/img_get.php?img=1&height=100&width=100&imgname='+shoplogo[0]"  alt="店铺图片" />
								<big-img v-if="showImg" @clickit="viewImg" :imgSrc="imgSrc"></big-img>
								<i class="el-icon-view" @click='clickImg'></i>
							</div>
							<img-upload @send-image='getPhoto' :ImageSize='imgSize'></img-upload>
							<div class="text-warp left">
								<p class="text">一个月内只能更换一次，文件类型必须为JPG、JPEG、PNG，宽500像素，高500像素，文件大小不得超过1M</p>
							</div>
					</el-form-item>
					<el-form-item label="联系人">
						<el-input v-model='form.contact'></el-input>
					</el-form-item>
					<el-form-item label="店铺面积" class='area'>
						<el-input v-model='form.shop_area'></el-input>
						<span class="pinfan">m²</span>
					</el-form-item>
					<el-form-item class='rel' label="地址">
						<el-input v-model='form.address'></el-input>
						<span class="ardess"></span>
					</el-form-item>
					<el-form-item label="门牌号">
						<el-input v-model='form.address_num'></el-input>
					</el-form-item>
					<div class="bottom">
						<button class="blue" type="button" @click='onSubmit'>保存</button>
						<button class="default" type="button" @click="goBack">取消</button>
					</div>
				</el-form>
			</div>

		</section>
	</div>
</template>

<script>
	import { saveShopinfo,getShopinfo} from '../../../api';
	import {  Confimshoparea, Character,Address } from '../../../index';
	import ImgUpload from '@/components/upload/SUpload';
	import BigImg from '../../../components/showBigImg';
	import { Util } from '@/config/util';
	export default {
		components:{
			ImgUpload,
			BigImg
		},

		data() {
			return {
				form: {
					shop_name: '',   //店铺 名字
					contact: '',    //联系人
					shop_area: '', //店铺面积
					address: '',  //店铺地址
					address_num: '' //店铺门牌号
				},
				shopinfo: {},   //用来接收请求过来的对象
				shoplogo:[],   //保存店铺标志
				imgbase_url: imgbase_url , //图片路径
				imgSize:1024,  //上传图片尺寸上限
				imgSrc:'',
				showImg:false
			};
		},
		created(){
			
			this.getData();
		},
		methods: {
/*保存店铺信息*/
			onSubmit() {
				if(!Confimshoparea(this.form.shop_area)){
					alert('请输入正确的面积信息！！')
					return;
				}
				if(!Character(this.form.shop_name)){
					alert('店铺名称只能是中文！！')
					return;
				}
				if(!Character(this.form.contact)){
					alert('联系人不能有数字！！')
					return;
				}
				if(!Confimshoparea(this.form.address_num)){
					alert('请输入正确的门牌号！！')
					return;
				}
				if(!Address(this.form.address)){
					alert('请输入正确的地址！！')
					return;
				}
				if(this.shoplogo.length==0){
					alert('必须上传图片！！')
					return;
				}
				let data = {
					shopinfo_save: 1,
					shop_initname: '毛毛快餐',
					shop_name: this.form.shop_name,
					contact: this.form.contact,
					shop_area: this.form.shop_area,
					address: this.form.address,
					address_num: this.form.address_num,
					shop_logo:this.shoplogo[0]
				};
				saveShopinfo(data, this.alts);
			},
			alts() {
				if(this.form.shop_name!=='' && this.imageUrl!=='' && this.form.contact!=='' && this.form.shop_area!=='' && this.form.address!=='' && this.form.address_num!==''){
					this.$router.go(-1);
				} else {
					alert('保存失败,所有内容必须填！！');
				}
				
			},
/*保存店铺信息*/
 //跳转到店铺信息
			goBack(){
				this.$router.go(-1);
			},
 //获取数据
			getData(){
				let _this = this;
				getShopinfo({
					get_shopinfo_base: 1
				}, function(resp) {
					if(resp){
						_this.shopinfo = resp.data.shopinfo||{};
						_this.form.shop_name = _this.shopinfo.shop_name;
						_this.form.contact = _this.shopinfo.contact;
						_this.form.shop_area = _this.shopinfo.shop_area;
						_this.form.address = _this.shopinfo.address;
						_this.form.address_num = _this.shopinfo.address_num; 
						if(!Util.isEmpty(_this.shopinfo.shop_logo)){
							_this.shoplogo.push(_this.shopinfo.shop_logo);
						}
						
					  }
			    });
		   },
//获取上传的的图片
		   getPhoto(res){
		   	this.shoplogo = [];
		   	this.shoplogo.push(res)
		   },
		    clickImg() {
                this.showImg = true;
                // 获取当前图片地址
                this.imgSrc = this.shoplogo[0];
           },
            viewImg(){
                this.showImg = false;
            }
}

};</script>

<style lang="scss" scoped>
	@import 'src/styles/mixin.scss';
	.warp {
		position:relative;
		.nvs {
			background: #EFF3FA;
			height: 26px;
		}
		background:#fff;
		.basicinformation {
			.title {
				@include fc($sizes, $grey);
				height: 40px;
				line-height: 40px;
				background: #F6F8FC;
				text-indent: 14px;
			}
			.info-warp {
				margin: 40px 0 0 148px;
				.shopTitle {
					@include fc(16px, #333333);
					font-weight: bold;
					margin-bottom: 18px;
					width: 90px;
    				text-align: right;
				}
				.shoplogo {
					@include wh(100px, 100px);
					img{
						width:100%;
						height: 100%;
						display: block;
					}
				}
				.shoplogo:hover .bg{
					position: absolute;
					left:0px;
					top:0px;
					@include wh(100px,100px);
					background: rgba(0,0,0,.5);
				}
				.shopInfo {
					.shops {
						@include fc(14px, #000000);
						margin-left: 48px;
						margin-bottom: 20px;
						.shopNumber {
							padding-left: 14px;
						}
					}
				}
				.area{
					position: relative;
					.pinfan{
						position: absolute;
						left: 290px;
						top: 0;
					}
				}
				.picInfo {
					text-indent: 0;
					padding-left: 20px;
				}
				.text-warp {
				
					.text{
						position: absolute;
						left:122px;
						top:22px;
						width: 280px;
					}
				}
				.text {
					padding-top: 20px;
					@include fc(12px, #9b9b9b);
					line-height: 16px;
					white-space: inherit;
				}
				.rel {
					position: relative;
					.ardess {
						position: absolute;
						left: 310px;
						top: 0;
						@include wh(32px, 32px);
						background: url(../../img/arrdess.png)no-repeat;
					}
				}
				button {
					cursor: pointer;
				}
				.bottom {
					width: 80%;
					text-align: center;
					margin-top: 140px;
					margin-left: 40px;
					margin-bottom: 50px;
					.blue {
						@include fc(16px, #ffffff);
						background: #4877E7;
						@include wh(160px, 40px);
						line-height: 40px;
						border-radius: 6px;
					}
					.default {
						@include fc(14px, #4877E7);
						background: #ffffff;
						@include wh(160px, 40px);
						line-height: 40px;
						border-radius: 6px;
						border: 1px solid #4877E7;
						margin-left: 50px;
					}
				}
			}
		}
	}
</style>
<style lang="scss">
	@import 'src/styles/mixin.scss';
	#editor {
		.el-form {
			overflow: hidden;
		}
		.el-button {
			@include wh(60px, 30px);
			background: #ffffff;
			@include fc(14px, #4877E7);
			line-height: 30px;
			border-radius: 0;
			padding: 0;
		}
		.el-input__inner {
			border: 1px solid #d9d9d9;
			border-radius: 0;
			@include wh(280px, 34px);
		}
		.el-form-item__label {
			padding: 11px 0;
		}
		.el-form-item {
			margin-bottom: 20px;
			.el-form-item__content{
				margin-left: 104px !important;
			}
		}
		.el-breadcrumb__item__inner,
		.el-breadcrumb__item__inner a {
			color: #333;
			font-size: 12px;
		}
		.el-breadcrumb__separator {
			margin: 0 5px;
			color: #333;
		}
		.el-breadcrumb__item:last-child .el-breadcrumb__item__inner,
		.el-breadcrumb__item:last-child .el-breadcrumb__item__inner a,
		.el-breadcrumb__item:last-child .el-breadcrumb__item__inner a:hover,
		.el-breadcrumb__item:last-child .el-breadcrumb__item__inner:hover {
			color: #4877E7;
		}
		.upload-btn{
			margin-left: 20px;
		}
		.img-view .img img {
  width: 600px;
    height: 400px;
    display: block;
    position: absolute;
    left:106px;
    right: 50px;
    margin: auto;
    z-index: 1001;
    bottom:-146px;
}
.el-icon-view{
				position: relative;
    			&:before{
		    		position: absolute;
		    		opacity: 0;
		    		left: 40px;
		    		top:-80px;
		    		font-size: 20px;
		    		color:#fff;
		    		cursor: pointer;
		    	}
				
			}
.shoplogo:hover .el-icon-view{
				position: relative;
    			&:before{
		    		position: absolute;
		    		opacity: 1;
		    		left: 40px;
		    		top:-80px;
		    		font-size: 20px;
		    		color:#fff;
		    		cursor: pointer;
		    	}
				
			}
		    
	}
</style>


