<template>
  <!-- 图片上传   -->
  <!-- 
    ImageSize: {
      type: Number,
      default: 512,
    }
    调用监听时间 this.$emit("send-image", resp.data.filename); 

  -->
  <div id="SlUpload">  
    <!-- 遮罩层 -->  
    <div class="container" v-show="panel">  
      <div>  
        <img ref="image" id="image" :src="url" alt="Picture">  
      </div>  
      <button type="button" class="button" @click="crop">确定</button>            
    </div>  
  
    <div class="input-btn">
        <span class="upload-btn" @click="inputClick">上传</span>  
        <input type="file" ref="inputer" id="change" accept="image" @change="change">            
    </div>  
  </div>  
</template>  
  
<script>
import Cropper from "cropperjs";
import { Public } from "@/config/public";

export default {
  props: {
    ImageSize: {
      type: Number,
      default: 512
    }
  },
  components: {},
  data() {
    return {
      picValue: "", //裁剪文件
      cropper: "",
      croppable: false,
      panel: false, //遮罩层
      url: ""
    };
  },
  mounted() {
    this.init();
  },
  methods: {
    init() {
      //初始化这个裁剪框
      let self = this;
      // let image = document.getElementById("image");
      // console.log(image);
      // console.log(this.$refs['image']);
      let image = this.$refs['image'];
      this.cropper = new Cropper(image, {
        //   aspectRatio: 1, //裁剪框比例 1：1
        dragMode: "move",
        toggleDragModeOnDblclick: false,
        viewMode: 1,
        background: true, // 容器是否显示网格背景
        zoomable: true, //是否允许缩放图片。
        movable: true, //是否能移动图片
        ready: function() {
          self.croppable = true;
        }
      });
    },
    //模拟点击
    inputClick() {
      this.$refs["inputer"].click();
    },
    //取得上传图片
    change(e) {
      let files = e.target.files || e.dataTransfer.files;
      if (!files.length) return;
      this.picValue = files[0];

      if (this.check(this.picValue)) {
        return;
      }

      this.url = this.getObjectURL(this.picValue);
      //每次替换图片要重新得到新的url
      if (this.cropper) {
        this.cropper.replace(this.url);
      }
      this.panel = true;
      this.$refs["inputer"].value = "";
    },

    getObjectURL(file) {
      let url = null;
      if (window.createObjectURL !== undefined) {
        // basic
        url = window.createObjectURL(file);
      } else if (window.URL !== undefined) {
        // mozilla(firefox)
        url = window.URL.createObjectURL(file);
      } else if (window.webkitURL !== undefined) {
        // webkit or chrome
        url = window.webkitURL.createObjectURL(file);
      }
      return url;
    },

    crop() {
      let croppedCanvas;

      this.panel = false;

      if (!this.croppable) {
        return;
      }


      // Crop
      croppedCanvas = this.cropper.getCroppedCanvas();

      croppedCanvas.toBlob(blob => {
        let args = {};
        let formData = new FormData();

        args["upload"] = 1;
        args["userid"] = Public.getUserid();

        for (var i in args) {
          formData.append(i, args[i]);
        }
        formData.append("imgfile", blob, "picture.jpg");

        $.ajax({
          url: base_url + "/img_save.php",
          type: "POST",
          cache: false,
          data: formData,
          dataType: "json",
          processData: false,
          contentType: false,

          success: resp => {
            if (resp.ret === 0) {
              this.$emit("send-image", resp.data.filename);
              // this.$notify({
              //   title: "成功",
              //   message: "图片上传成功！",
              //   type: "success"
              // });
            } else {
              this.$notify({
                title: "警告",
                message: "图片上传失败！",
                type: "warning"
              });
            }
          },
          error: () => {
            this.$notify({
              title: "警告",
              message: "图片上传失败！",
              type: "warning"
            });
          }
        });
      }, "image/jpeg");
    },

    //校验图片
    check(file) {
      let accept = ["image/jpeg", "image/png"];

      if (accept.indexOf(file.type) === -1) {
        this.$notify({
          title: "警告",
          message: "图片格式错误！",
          type: "warning"
        });

        return true;
      }

      if (file.size > this.ImageSize * 1024) {
        this.$notify({
          title: "警告",
          message: "您选择的图片过大，请选择500k以内的图片",
          type: "warning"
        });
        return true;
      }
    }
  }
};
</script>  
  
<style lang="scss" scoped>
@import "src/styles/mixin.scss";

#SlUpload {
  display: inline-block;
}
#SlUpload .input-btn {
  display: inline-block;
}

#SlUpload .upload-btn {
  @include fc(14px, #4877e7);
  @include wh(60px, 30px);
  display: inline-block;
  line-height: 30px;
  text-align: center;
  border-radius: 2px;
  border: 1px solid #4877e7;
  cursor: pointer;
}

#change {
  display: none;
}

#SlUpload .button {
  position: absolute;
  right: 10px;
  top: 10px;
  width: 80px;
  height: 40px;
  border: none;
  border-radius: 5px;
  background: white;
  cursor: pointer;
}

#SlUpload .picture {
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
}
#SlUpload .container {
  z-index: 1099;
  position: fixed;
  padding-top: 60px;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 1);
}

#SlUpload #image {
  max-width: 100%;
}
</style>
<style>
/* .cropper-view-box,
.cropper-face {
  border-radius: 50%;
} */
/*!  
 * Cropper.js v1.0.0-rc  
 * https://github.com/fengyuanchen/cropperjs  
 *  
 * Copyright (c) 2017 Fengyuan Chen  
 * Released under the MIT license  
 *  
 * Date: 2017-03-25T12:02:21.062Z  
 */

.cropper-container {
  font-size: 0;
  line-height: 0;
  position: relative;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  direction: ltr;
  -ms-touch-action: none;
  touch-action: none;
}

.cropper-container img {
  /* Avoid margin top issue (Occur only when margin-top <= -height) */
  display: block;
  min-width: 0 !important;
  max-width: none !important;
  min-height: 0 !important;
  max-height: none !important;
  width: 100%;
  height: 100%;
  image-orientation: 0deg;
}

.cropper-wrap-box,
.cropper-canvas,
.cropper-drag-box,
.cropper-crop-box,
.cropper-modal {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.cropper-wrap-box {
  overflow: hidden;
}

.cropper-drag-box {
  opacity: 0;
  background-color: #fff;
}

.cropper-modal {
  opacity: 0.5;
  background-color: #000;
}

.cropper-view-box {
  display: block;
  overflow: hidden;

  width: 100%;
  height: 100%;

  outline: 1px solid #39f;
  outline-color: rgba(51, 153, 255, 0.75);
}

.cropper-dashed {
  position: absolute;

  display: block;

  opacity: 0.5;
  border: 0 dashed #eee;
}

.cropper-dashed.dashed-h {
  top: 33.33333%;
  left: 0;
  width: 100%;
  height: 33.33333%;
  border-top-width: 1px;
  border-bottom-width: 1px;
}

.cropper-dashed.dashed-v {
  top: 0;
  left: 33.33333%;
  width: 33.33333%;
  height: 100%;
  border-right-width: 1px;
  border-left-width: 1px;
}

.cropper-center {
  position: absolute;
  top: 50%;
  left: 50%;

  display: block;

  width: 0;
  height: 0;

  opacity: 0.75;
}

.cropper-center:before,
.cropper-center:after {
  position: absolute;
  display: block;
  content: " ";
  background-color: #eee;
}

.cropper-center:before {
  top: 0;
  left: -3px;
  width: 7px;
  height: 1px;
}

.cropper-center:after {
  top: -3px;
  left: 0;
  width: 1px;
  height: 7px;
}

.cropper-face,
.cropper-line,
.cropper-point {
  position: absolute;

  display: block;

  width: 100%;
  height: 100%;

  opacity: 0.1;
}

.cropper-face {
  top: 0;
  left: 0;

  background-color: #fff;
}

.cropper-line {
  background-color: #39f;
}

.cropper-line.line-e {
  top: 0;
  right: -3px;
  width: 5px;
  cursor: e-resize;
}

.cropper-line.line-n {
  top: -3px;
  left: 0;
  height: 5px;
  cursor: n-resize;
}

.cropper-line.line-w {
  top: 0;
  left: -3px;
  width: 5px;
  cursor: w-resize;
}

.cropper-line.line-s {
  bottom: -3px;
  left: 0;
  height: 5px;
  cursor: s-resize;
}

.cropper-point {
  width: 5px;
  height: 5px;

  opacity: 0.75;
  background-color: #39f;
}

.cropper-point.point-e {
  top: 50%;
  right: -3px;
  margin-top: -3px;
  cursor: e-resize;
}

.cropper-point.point-n {
  top: -3px;
  left: 50%;
  margin-left: -3px;
  cursor: n-resize;
}

.cropper-point.point-w {
  top: 50%;
  left: -3px;
  margin-top: -3px;
  cursor: w-resize;
}

.cropper-point.point-s {
  bottom: -3px;
  left: 50%;
  margin-left: -3px;
  cursor: s-resize;
}

.cropper-point.point-ne {
  top: -3px;
  right: -3px;
  cursor: ne-resize;
}

.cropper-point.point-nw {
  top: -3px;
  left: -3px;
  cursor: nw-resize;
}

.cropper-point.point-sw {
  bottom: -3px;
  left: -3px;
  cursor: sw-resize;
}

.cropper-point.point-se {
  right: -3px;
  bottom: -3px;
  width: 20px;
  height: 20px;
  cursor: se-resize;
  opacity: 1;
}

@media (min-width: 768px) {
  .cropper-point.point-se {
    width: 15px;
    height: 15px;
  }
}

@media (min-width: 992px) {
  .cropper-point.point-se {
    width: 10px;
    height: 10px;
  }
}

@media (min-width: 1200px) {
  .cropper-point.point-se {
    width: 5px;
    height: 5px;
    opacity: 0.75;
  }
}

.cropper-point.point-se:before {
  position: absolute;
  right: -50%;
  bottom: -50%;
  display: block;
  width: 200%;
  height: 200%;
  content: " ";
  opacity: 0;
  background-color: #39f;
}

.cropper-invisible {
  opacity: 0;
}

.cropper-bg {
  background-image: url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABAAAAAQAQMAAAAlPW0iAAAAA3NCSVQICAjb4U/gAAAABlBMVEXMzMz////TjRV2AAAACXBIWXMAAArrAAAK6wGCiw1aAAAAHHRFWHRTb2Z0d2FyZQBBZG9iZSBGaXJld29ya3MgQ1M26LyyjAAAABFJREFUCJlj+M/AgBVhF/0PAH6/D/HkDxOGAAAAAElFTkSuQmCC");
}

.cropper-hide {
  position: absolute;

  display: block;

  width: 0;
  height: 0;
}

.cropper-hidden {
  display: none !important;
}

.cropper-move {
  cursor: move;
}

.cropper-crop {
  cursor: crosshair;
}

.cropper-disabled .cropper-drag-box,
.cropper-disabled .cropper-face,
.cropper-disabled .cropper-line,
.cropper-disabled .cropper-point {
  cursor: not-allowed;
}
</style> 


